.login-form-container {
  width: 500px;
  height: 400px;
  margin: auto;
  background-color: white;
  padding: 32px;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
  text-align: center;
}

.login-form {
  margin: 48px auto;
}

.login-logo {
  width: 250px;
}

.menu-header {
  padding: 16px 16px 0px 24px;
}

.menu-header:hover {
  cursor: pointer;
}

.menu-header-facility {
  color: white;
  font-weight: bold;
}
.menu-header-name {
  color: #ffffffa6;
}

.video-wrapper {
  background-color: #1f1f23;
  border-radius: 8px;
}
.video-commands-overlay {
  position: absolute;
  right: 0;
  z-index: 999;
}

.video-loading-spinner {
  margin: auto;
}

.video-loading-spinner-span {
  color: white;
}

.fc {
  /* the calendar root */
  max-width: 1100px;
  margin: 0 auto;
}

