// @import "./scss/custom.scss";
@import "~antd/dist/antd.css";
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@200;300;400;500;600;700;800&display=swap");

:root {
  --main-primary-color: #0089ff;
  --main-black-600: #393939;
  --main-black-500: #6d6d6d;
  --main-black-400: #9a9a9a;
  --main-black-300: #cdcdcd;
  --main-black-200: #f2f2f2;
  --main-black-100: #fbfbfb;
  --main-green: #2eba77;
  --main-lightgreen: #bbfcdd;
  --main-red: #df6161;
  --main-lightred: #f5c3c3;
  --main-lightyellow: #faf5e3;
  --main-blue-600: #004886;
  --main-blue-500: #0275d8;
  --main-blue-400: #0089ff;
  --main-blue-300: #70a9e9;
  --main-blue-200: #9ac1ec;
  --main-blue-100: #eef6ff;
  --main-lightpurple: #e7dff0;
  --main-background: #fafafb;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  // font-family: "Rubik", sans-serif !important;
  color: var(--main-black-600) !important;
  background-color: var(--main-background) !important;
}

/* Typography */
.p1 {
  font-size: 40px;
}

.p2 {
  font-size: 32px;
}

.p3 {
  font-size: 24px;
}

.p4 {
  font-size: 18px;
}

.p5 {
  font-size: 16px;
}

.p6 {
  font-size: 14px;
}
.p7 {
  font-size: 12px;
}

.font-weight-semibold {
  font-weight: 600;
}

.font-weight-medium {
  font-weight: 500;
}

@media (min-width: 768px) {
  .p1 {
    font-size: 48px;
  }

  .p2 {
    font-size: 36px;
  }

  .p3 {
    font-size: 24px;
  }

  .p4 {
    font-size: 18px;
  }

  .p5 {
    font-size: 16px;
  }

  .p6 {
    font-size: 14px;
  }
}

/* Common Layouts */
.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.m-auto {
  margin: auto;
}

.vh-100 {
  height: 100vh;
}

.vw-100 {
  width: 100vw;
}
